import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ILivroModel } from '../Models/livroModel';

@Injectable({
  providedIn: 'root'
})
export class LivrosService {

  constructor(public http: HttpClient) { }

  getAll(): Observable<ILivroModel[]> {
    return this.http.get<ILivroModel[]>('https://localhost:44364/api/livro/RecuperarLivros');
  }

  getByCodigo(codigoLivro): Observable<ILivroModel> {
    return this.http.get<ILivroModel>('https://localhost:44364/api/livro/RecuperarLivro?codigoLivro=' + codigoLivro);
  }

  getLivrosDisponiveis(): Observable<ILivroModel[]> {
    return this.http.get<ILivroModel[]>('https://localhost:44364/api/livro/RecuperarLivrosDisponiveisEmprestimo');
  }

  salvarLivro(livro: ILivroModel): Observable<any> {
    if (livro?.codigoLivro) {
      return this.http.put('https://localhost:44364/api/livro/AlterarLivro', livro);
    }
    livro.codigoLivro = 0;
    return this.http.post('https://localhost:44364/api/livro/Incluirlivro', livro);
  }

  inativarLivro(codigoLivro): Observable<any> {
    return this.http.put('https://localhost:44364/api/livro/Inativacaolivro?codigoLivro=' + codigoLivro, null);
  }

}
