import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { IEmprestimoModel } from './../Models/emprestimoModel';
import { EmprestimoService } from './../services/emprestimoService';

@Component({
  selector: 'app-extrapolar',
  templateUrl: './extrapolar.component.html',
  styleUrls: ['./extrapolar.component.css']
})
export class ExtrapolarComponent implements OnInit {

  public emprestimos: IEmprestimoModel[] = [];
  public emprestimosQtde = null;

  constructor(public emprestimosService: EmprestimoService, public router: Router) { }

  ngOnInit(): void {
    this.emprestimosService.getAllExtrapolar().subscribe((emprestimos: IEmprestimoModel[]) => {
      this.emprestimos = emprestimos['data'];
      this.emprestimosQtde = emprestimos['total'];
    });
  }

}
