import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { IEmprestimoModel } from './../Models/emprestimoModel';
import { Injectable, ResolvedReflectiveFactory } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class EmprestimoService {

  constructor(public http: HttpClient) { }

  getAll(): Observable<IEmprestimoModel[]> {
    return this.http.get<IEmprestimoModel[]>('https://localhost:44364/api/emprestimo/RecuperarEmprestimos');
  }

  getAllExtrapolar(): Observable<IEmprestimoModel[]> {
    return this.http.get<IEmprestimoModel[]>('https://localhost:44364/api/emprestimo/ValidaUsuarioEmprestimoExtrapolado');
  }

  getByCodigo(codigoEmprestimo): Observable<IEmprestimoModel> {
    return this.http.get<IEmprestimoModel>('https://localhost:44364/api/emprestimo/RecuperarEmprestimo?codigoEmprestimo=' + codigoEmprestimo);
  }

  salvarEmprestimo(emprestimo: IEmprestimoModel): Observable<any> {
    if (emprestimo?.codigoEmprestimo) {
      return this.http.put('https://localhost:44364/api/emprestimo/AlterarEmprestimo', emprestimo);
    }
    emprestimo.codigoEmprestimo = 0;
    return this.http.post('https://localhost:44364/api/emprestimo/IncluirEmprestimo', emprestimo);
  }

  DevolverEmprestimo(codigoEmprestimo): Observable<any> {
    return this.http.put('https://localhost:44364/api/emprestimo/DevolvidoEmprestimo?codigoEmprestimo=' + codigoEmprestimo, null);
  }

  ReservarLivro(codigoEmprestimo): Observable<any> {
    return this.http.put('https://localhost:44364/api/reserva/IncluirLivroReserva?codigoEmprestimo=' + codigoEmprestimo, null);
  }

  validaQtdeMaximo(codigoUsuario): Observable<IEmprestimoModel> {
    return this.http.get<IEmprestimoModel>('https://localhost:44364/api/emprestimo/ValidaUsuarioEmpresteMaximo?codigoUsuraio' + codigoUsuario);
  }

}
