import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { IUsuarioModel } from '../Models/usuarioModel';
import { UsuariosService } from './../services/usuarios.service';

@Component({
  selector: 'app-usuarios',
  templateUrl: './usuarios.component.html',
  styleUrls: ['./usuarios.component.css']
})
export class UsuariosComponent implements OnInit {

  public usuarios: IUsuarioModel[] = [];
  public usuariosQtde = null;

  constructor(public usuarioService: UsuariosService, public router: Router) { }

  ngOnInit(): void {
    this.usuarioService.getAll().subscribe((usuarios: IUsuarioModel[]) => {
      this.usuarios = usuarios['data'];
      this.usuariosQtde = usuarios['total'];
    });
  }

  novoUsuario() {
    this.router.navigate(["usuario", 0]);
  }

  editarUsuario(codigoUsuario) {
    this.router.navigate(["usuario", codigoUsuario]);
  }

  inativarUsuario(codigoUsuario) {
    this.usuarioService.inativarUsuario(codigoUsuario).subscribe(() => {
      alert("Usuário inativado com sucesso!");
      window.location.reload();
     });
  }

}
