import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { BrowserModule } from '@angular/platform-browser';
import { LivrosComponent } from './livros/livros.component';
import { UsuariosComponent } from './usuarios/usuarios.component';
import { ExtrapolarComponent } from './extrapolar/extrapolar.component';
import { InstituicaoComponent } from './instituicao/instituicao.component';
import { EmprestimosComponent } from './emprestimos/emprestimos.component';
import { FormLivrosComponent } from './livros/form-livros/form-livros.component';
import { FormUsuariosComponent } from './usuarios/form-usuarios/form-usuarios.component';
import { FormEmprestimosComponent } from './emprestimos/form-emprestimos/form-emprestimos.component';
import { FormInstituicoesComponent } from './instituicao/form-instituicoes/form-instituicoes.component';

const routes: Routes = [
  { path: 'usuarios', component: UsuariosComponent },
  { path: 'usuario/:id', component: FormUsuariosComponent },
  { path: 'livros', component: LivrosComponent },
  { path: 'livro/:id', component: FormLivrosComponent },
  { path: 'instituicao', component: InstituicaoComponent },
  { path: 'instituicao/:id', component: FormInstituicoesComponent },
  { path: 'emprestimos', component: EmprestimosComponent },
  { path: 'emprestimo/:id', component: FormEmprestimosComponent },
  { path: 'extrapolar', component: ExtrapolarComponent }
]

@NgModule({
  declarations: [
    AppComponent,
    UsuariosComponent,
    LivrosComponent,
    InstituicaoComponent,
    FormUsuariosComponent,
    FormLivrosComponent,
    FormInstituicoesComponent,
    EmprestimosComponent,
    FormEmprestimosComponent,
    ExtrapolarComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes),
    HttpClientModule,
    ReactiveFormsModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
