import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { IUsuarioModel } from './../../Models/usuarioModel';
import { UsuariosService } from './../../services/usuarios.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { InstituicoesService } from './../../services/instituicao.service';
import { IInstituicaoEnsinoModel } from './../../Models/InstituicaoEnsinoModel';

@Component({
  selector: 'app-form-usuarios',
  templateUrl: './form-usuarios.component.html',
  styleUrls: ['./form-usuarios.component.css']
})
export class FormUsuariosComponent implements OnInit {

  public codigoUsuario: number;
  public instituicoes: IInstituicaoEnsinoModel[] = [];
  public instituicoesQtde = null;
  public usuario: IUsuarioModel;
  public usuariosQtde = null;

  public formUsuario: FormGroup;

  constructor(public instituicaoService: InstituicoesService, public usuarioService: UsuariosService, public activated: ActivatedRoute, private _location: Location) { }

  ngOnInit(): void {
    this.formUsuario = new FormGroup({
      codigoUsuario: new FormControl(''),
      codigoInstituicaoEnsino: new FormControl(0, [Validators.required]),
      nomeUsuario: new FormControl('', [Validators.required]),
      enderecoUsuario: new FormControl(''),
      cpfUsuario: new FormControl(''),
      telefoneUsuario: new FormControl(''),
      emailUsuario: new FormControl(''),
      ativoUsuario: new FormControl(1),
    });

    this.activated.params.subscribe(({id}) => {
      this.codigoUsuario = id;
      if (this.codigoUsuario != 0) {
        document.getElementById("trCodigo").style.display = "";
      } else {
        document.getElementById("trCodigo").style.display = "none";
      }
    });

    this.instituicaoService.getAllAtivos().subscribe((instituicoes: IInstituicaoEnsinoModel[]) => {
      this.instituicoes = instituicoes['data'];
      this.instituicoesQtde = instituicoes['total'];
    });

    this.usuarioService.getByCodigo(this.codigoUsuario).subscribe((usuario: IUsuarioModel) => {
      this.usuario = usuario;
      this.formUsuario.patchValue(this.usuario);
      this.usuariosQtde = usuario['total'];
    });
  }

  salvar() {
    console.log(this.formUsuario.value);  // this.formUsuario.value;
     this.usuarioService.salvarUsuario(this.formUsuario.value).subscribe(() => {
      alert("Usuário Salvo com sucesso!");
      this._location.back();
     });
  }

  voltar() {
    this._location.back();
  }

}
