import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { ILivroModel } from './../../Models/livroModel';
import { LivrosService } from './../../services/livros.services';
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-form-livros',
  templateUrl: './form-livros.component.html',
  styleUrls: ['./form-livros.component.css']
})
export class FormLivrosComponent implements OnInit {

  public codigoLivro: number;
  public livro: ILivroModel;
  public livrosQtde = null;

  public formLivro: FormGroup;

  constructor(public livrosService: LivrosService, public activated: ActivatedRoute, private _location: Location) { }

  ngOnInit(): void {

    this.formLivro = new FormGroup({
      codigoLivro: new FormControl(''),
      tituloLivro: new FormControl('', [Validators.required]),
      autorLivro: new FormControl('', [Validators.required]),
      sinopseLivro: new FormControl(''),
      capaLivro: new FormControl(''),
      ativoLivro: new FormControl(1),
    });

    this.activated.params.subscribe(({id}) => {
      this.codigoLivro = id;
      if (this.codigoLivro != 0) {
        document.getElementById("trCodigo").style.display = "";
      } else {
        document.getElementById("trCodigo").style.display = "none";
      }

      this.livrosService.getByCodigo(this.codigoLivro).subscribe((livro: ILivroModel) => {
        this.livro = livro;
        this.formLivro.patchValue(this.livro);
        this.livrosQtde = livro['total'];
      });

    });

  }

  salvar() {
    console.log(this.formLivro.value);  // this.formUsuario.value;
     this.livrosService.salvarLivro(this.formLivro.value).subscribe(() => {
      alert("Livro Salvo com sucesso!");
      this._location.back();
     });
  }

  voltar() {
    this._location.back();
  }

}
