import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IUsuarioModel } from '../Models/usuarioModel';

@Injectable({
  providedIn: 'root'
})
export class UsuariosService {

  constructor(public http: HttpClient) { }

  getAll(): Observable<IUsuarioModel[]> {
    return this.http.get<IUsuarioModel[]>('https://localhost:44364/api/usuario/RecuperarUsuarios');
  }

  getByCodigo(codigoUsuario): Observable<IUsuarioModel> {
    return this.http.get<IUsuarioModel>('https://localhost:44364/api/usuario/RecuperarUsuario?codigoUsuario=' + codigoUsuario);
  }

  getUsuariosDisponiveis(): Observable<IUsuarioModel[]> {
    return this.http.get<IUsuarioModel[]>('https://localhost:44364/api/usuario/RecuperarUsuariosDisponiveisEmprestimo');
  }

  salvarUsuario(usuario: IUsuarioModel): Observable<any> {
    if (usuario?.codigoUsuario) {
      return this.http.put('https://localhost:44364/api/usuario/AlterarUsuario', usuario);
    }
    usuario.codigoUsuario = 0;
    return this.http.post('https://localhost:44364/api/usuario/IncluirUsuario', usuario);
  }

   inativarUsuario(codigoUsuario): Observable<any> {
      return this.http.put('https://localhost:44364/api/usuario/InativacaoUsuario?codigoUsuario=' + codigoUsuario, null);
   }

}
