import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { InstituicoesService } from './../../services/instituicao.service';
import { IInstituicaoEnsinoModel } from './../../Models/InstituicaoEnsinoModel';

@Component({
  selector: 'app-form-instituicoes',
  templateUrl: './form-instituicoes.component.html',
  styleUrls: ['./form-instituicoes.component.css']
})
export class FormInstituicoesComponent implements OnInit {

  public codigoInstituicaoEnsino: number;
  public instituicao: IInstituicaoEnsinoModel;
  public instituicaoQtde = null;

  public formInstituicao: FormGroup;

  constructor(public instituicoesService: InstituicoesService, public activated: ActivatedRoute, private _location: Location) { }

  ngOnInit(): void {
    this.formInstituicao = new FormGroup({
      codigoInstituicaoEnsino: new FormControl(''),
      nomeInstituicaoEnsino: new FormControl('', [Validators.required]),
      enderecoInstituicaoEnsino: new FormControl('', [Validators.required]),
      cnpjInstituicaoEnsino: new FormControl(''),
      telefoneInstituicaoEnsino: new FormControl(''),
      ativoInstituicaoEnsino: new FormControl(1),
    });

    this.activated.params.subscribe(({id}) => {
      this.codigoInstituicaoEnsino = id;
      if (this.codigoInstituicaoEnsino != 0) {
        document.getElementById("trCodigo").style.display = "";
      } else {
        document.getElementById("trCodigo").style.display = "none";
      }

      this.instituicoesService.getByCodigo(this.codigoInstituicaoEnsino).subscribe((instituicao: IInstituicaoEnsinoModel) => {
        this.instituicao = instituicao;
        this.formInstituicao.patchValue(this.instituicao);
        this.instituicaoQtde = instituicao['total'];
      });

    });

  }

  salvar() {
     console.log(this.formInstituicao.value);
     this.instituicoesService.salvarInstituicao(this.formInstituicao.value).subscribe(() => {
      alert("Instituição de ensino salvo com sucesso!");
      this._location.back();
     });
  }

  voltar() {
    this._location.back();
  }

}
