import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { InstituicoesService } from './../services/instituicao.service';
import { IInstituicaoEnsinoModel } from './../Models/InstituicaoEnsinoModel';

@Component({
  selector: 'app-instituicao',
  templateUrl: './instituicao.component.html',
  styleUrls: ['./instituicao.component.css']
})
export class InstituicaoComponent implements OnInit {

  public instituicoes: IInstituicaoEnsinoModel[] = [];
  public instituicoesQtde = null;

  constructor(public instituicaoService: InstituicoesService, public router: Router) { }

  ngOnInit(): void {
    this.instituicaoService.getAll().subscribe((instituicoes: IInstituicaoEnsinoModel[]) => {
      this.instituicoes = instituicoes['data'];
      this.instituicoesQtde = instituicoes['total'];
    });
  }

  novaInstituicao() {
    this.router.navigate(["instituicao",0]);
  }

  editarInstituicao(codigoInstituicao) {
    this.router.navigate(["instituicao",codigoInstituicao]);
  }

  inativarInstituicao(codigoInstituicao) {
    this.instituicaoService.inativarInstituicao(codigoInstituicao).subscribe(() => {
      alert("Instituição de ensino inativado com sucesso!");
      window.location.reload();
     });
  }

}
