import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { ILivroModel } from '../Models/livroModel';
import { LivrosService } from './../services/livros.services';

@Component({
  selector: 'app-livros',
  templateUrl: './livros.component.html',
  styleUrls: ['./livros.component.css']
})
export class LivrosComponent implements OnInit {

  public livros: ILivroModel[] = [];
  public livrosQtde = null;

  constructor(public livroService: LivrosService, public router: Router) { }

  ngOnInit(): void {
    this.livroService.getAll().subscribe((livros: ILivroModel[]) => {
      this.livros = livros['data'];
      this.livrosQtde = livros['total'];
    });
  }

  novoLivro() {
    this.router.navigate(["livro",0]);
  }

  editarLivro(codigoLivro) {
     this.router.navigate(["livro",codigoLivro]);
   }

  inativarLivro(codigoLivro) {
    this.livroService.inativarLivro(codigoLivro).subscribe(() => {
      alert("Livro inativado com sucesso!");
      window.location.reload();
     });
  }

}
