import { Location } from '@angular/common';
import { ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { ILivroModel } from './../../Models/livroModel';
import { IUsuarioModel } from './../../Models/usuarioModel';
import { LivrosService } from './../../services/livros.services';
import { IEmprestimoModel } from './../../Models/emprestimoModel';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { EmprestimoService } from './../../services/emprestimoService';
import { UsuariosService } from './../../services/usuarios.service';

@Component({
  selector: 'app-form-emprestimos',
  templateUrl: './form-emprestimos.component.html',
  styleUrls: ['./form-emprestimos.component.css']
})
export class FormEmprestimosComponent implements OnInit {

  public codigoEmprestimo: number;
  public emprestimo: IEmprestimoModel;
  public emprestimosQtde = null;
  public usuarios: IUsuarioModel[] = [];
  public usuariosQtde = null;
  public livros: ILivroModel[] = [];
  public livrosQtde = null;
  public codUsuario = null;
  public emprestimosValue: IEmprestimoModel[] = [];

  public formEmprestimo: FormGroup;

  constructor(public emprestimoService: EmprestimoService, public usuarioService: UsuariosService, public livrosService: LivrosService,public activated: ActivatedRoute, private _location: Location) { }

  ngOnInit(): void {
    this.formEmprestimo = new FormGroup({
      codigoEmprestimo: new FormControl(0, [Validators.required]),
      codigoUsuario: new FormControl(0, [Validators.required]),
      codigoLivro: new FormControl(0, [Validators.required]),
      dataEmprestimo: new FormControl(null),
      devolvidoEmprestimo: new FormControl(0),
    });

    this.activated.params.subscribe(({id}) => {
      this.codigoEmprestimo = id;
      if (this.codigoEmprestimo != 0) {
        document.getElementById("trCodigo").style.display = "";
      } else {
        document.getElementById("trCodigo").style.display = "none";
      }
    });

    this.usuarioService.getUsuariosDisponiveis().subscribe((usuarios: IUsuarioModel[]) => {
      this.usuarios = usuarios['data'];
      this.usuariosQtde = usuarios['total'];
    });

    this.livrosService.getLivrosDisponiveis().subscribe((livros: ILivroModel[]) => {
      this.livros = livros['data'];
      this.livrosQtde = livros['total'];
    });

  }

  salvar() {
     console.log(this.formEmprestimo.value.codigoUsuario);
     this.emprestimoService.salvarEmprestimo(this.formEmprestimo.value).subscribe(() => {
      alert("Emprestimo salvo com sucesso!");
      this._location.back();
     });
  }

  voltar() {
    this._location.back();
  }

}
