import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IInstituicaoEnsinoModel } from './../Models/InstituicaoEnsinoModel';

@Injectable({
  providedIn: 'root'
})
export class InstituicoesService {

  constructor(public http: HttpClient) { }

  getAll(): Observable<IInstituicaoEnsinoModel[]> {
    return this.http.get<IInstituicaoEnsinoModel[]>('https://localhost:44364/api/instituicaoEnsino/RecuperarInstituicoesEnsino');
  }

  getAllAtivos(): Observable<IInstituicaoEnsinoModel[]> {
    return this.http.get<IInstituicaoEnsinoModel[]>('https://localhost:44364/api/instituicaoEnsino/RecuperarInstituicoesEnsinoAtivos');
  }

  getByCodigo(codigoInstituicaoEnsino): Observable<IInstituicaoEnsinoModel> {
    return this.http.get<IInstituicaoEnsinoModel>('https://localhost:44364/api/instituicaoEnsino/RecuperarInstituicaoEnsino?codigoInstituicaoEnsino=' + codigoInstituicaoEnsino);
  }

  salvarInstituicao(instituicao: IInstituicaoEnsinoModel): Observable<any> {
     if (instituicao?.codigoInstituicaoEnsino) {
       return this.http.put('https://localhost:44364/api/instituicaoEnsino/AlterarInstituicaoEnsino', instituicao);
     }
     instituicao.codigoInstituicaoEnsino = 0;
     return this.http.post('https://localhost:44364/api/instituicaoEnsino/IncluirInstituicaoEnsino', instituicao);
  }

  inativarInstituicao(codigoInstituicaoEnsino): Observable<any> {
      return this.http.put('https://localhost:44364/api/instituicaoEnsino/InativacaoInstituicaoEnsino?codigoInstituicaoEnsino=' + codigoInstituicaoEnsino, null);
  }

}
